import { defaultImageDetailsPage, defaultImageLicensingPage, defaultSearchPage } from './defaultPageConfigs';

const intialState = {
  inputs: {
    apiKey: '0OviQeVft2KY9BiFNJF8XRPvTe3ZXUFS',
    htmlElement: 'widget-container',
  },
  page: defaultSearchPage,
};


export default (prevState = { ...intialState }, action) => {
 switch (action.type) {
  case 'SIMPLE_ACTION':
   return {
    result: action.payload
   }

  case 'SET_PARAMS':
    return {
      ...prevState,
      inputs: {
        ...prevState.inputs,
        ...action.payload.inputs
      },
      page: {
        ...action.payload.page
      }
    }

  default:
    return prevState
 }
};
