import {
  onItemClickString as onItemClickCode,
  imageSearchFilters,
  overlayActionsString as overlayActionsCode,
} from './common-snippets';

import { getPageConfig } from './getPageConfig'

export const getHtmlCode = ({
  inputs: {
    apiKey,
    htmlElement,
  },
  page
}) => {
const pageConfig = getPageConfig(page);
const renderer = `widget.getPage('${page.name}').${page.name === 'searchPage' ? 'search()' : 'render();'}`
return `
<html>
  <head>
    <link rel="stylesheet" type="text/css" href="https://api-cdn.shutterstock.com/2.0.0/static/css/sstk-widget.css">
  </head>
  <body>
    <div id="${htmlElement}"></div>
    <script>
      window.onload = () => {
        const ${page.name} = ${pageConfig};

        const widget = new ShutterstockWidget({
          container: document.getElementById('${htmlElement}'),
          key: '${apiKey}',
          pages: [${page.name}]
        });

        ${renderer}
      }
    </script>
    <script src="https://api-cdn.shutterstock.com//2.0.0/static/js/sstk-widget.js"></script>
  </body>
</html>
`
};
