import React, { useState } from 'react';
import { Modal, Button, Form, Row, Col, InputGroup, FormControl, Collapse } from 'react-bootstrap';

import insertPreviewSvg from '../../images/insert-preview.svg';

import RadioButton from '../RadioButton';
import Checkbox from '../Checkbox';
import Input from '../Input';
import ExapandableList from '../ExapandableList';
import CodeSnippetBlock from '../CodeSnippetBlock';
import Editor from '../Editor';
import PageConfigModal from '../PagesConfig/PageConfigModal';

import classes from '../LeftNavMenu/LeftNavMenu.module.scss';

const defaultOverlayActionCode = `[
  {
    label: 'Insert preview',
    icon:'${insertPreviewSvg}',
    onClick: (e, item) => {
      e.preventDefault();
      console.log(item);
    }
  }
]`;

const defaultOnItemClick = `(e, item) => {
  e.preventDefault();
  console.log(item);
}`;

const defaultSearchFilter = `{
  showFilterDrawer: true,
  images: {
    imageTypeFilter: true,
    orientationFilter: true
  }
}`;


const SearchPageConfig = ({
  page,
  setParams,
  activated
}) => {
  console.log({ activated })
  // console.log(page);
  const [mediaType, setMediaType] = useState(page.props.mediaType);
  const [widgetSubtitle, setWidgetSubtitle] = useState(page.props.widgetSubtitle);
  const [widgetTitle, setWidgetTitle] = useState(page.props.widgetTitle);

  const [dynamicSubtitle, setDynamicSubtitle] = useState(page.props.dynamicSubtitle);
  const [dynamicTitle, setDynamicTitle] = useState(page.props.dynamicTitle);
  const [showMore, setShowMore] = useState(page.props.showMore);
  const [showSearchBar, setShowSearchBar] = useState(page.props.showSearchBar);

  const [assetsPerPage, setAssetsPerPage] = useState(page.props.assetsPerPage);

  const [onItemClick, setOnItemClick] = useState(page.props.onItemClick);

  const [searchFilters, setSearchFilters] = useState(page.props.searchFilters);
  const [overlayActions, setOverlayActions] = useState(page.props.overlayActions);

  const[overlayActionCode, setOverlayActionCode] = useState(defaultOverlayActionCode);

  const [open, setOpen] = useState({
    options: true,
    mediaType: true,
    htmlContainer: true,
    apiKey: true,
    titleAndSubtitle: true,
    callbacks: true,
  });

  const [show, setShow] = useState(false);

  return (
    <>
      <div>
        {activated && (<a href="#" onClick={(e) => {
          e.preventDefault();
          setShow(true)
        }}> Edit</a>)}
      </div>
      <PageConfigModal show={show} setShow={setShow} pageDisplayName="Search Page">
        <Form>
          <ExapandableList
            heading="Select media type"
            classes={classes}
            open={open.mediaType}
            onClick={() => setOpen({ ...open, mediaType: !open.mediaType })}
          >
            <Form.Group onChange={(e) => setMediaType(e.target.value)}>
              <RadioButton label="Images" name="mediaType" id="images" value="images" defaultChecked={mediaType === "images"} />
              <RadioButton label="Videos" name="mediaType" id="videos" value="videos" defaultChecked={mediaType === "videos"} />
            </Form.Group>
          </ExapandableList>
          {/* {<ExapandableList
            heading="Title & Subtitle"
            classes={classes}
            open={open.titleAndSubtitle}
            onClick={() => setOpen({ ...open, titleAndSubtitle: !open.titleAndSubtitle })}
          >
            <h6>Title</h6>
            <Input value={widgetTitle} onChange={(e) => setWidgetTitle(e.target.value)} />
            <h6>Subtitle</h6>
            <Input value={widgetSubtitle} onChange={(e) => setWidgetSubtitle(e.target.value)} />
            <small className="form-text text-muted">Enter custom text (uncheck dynamic titles)</small>
          </ExapandableList> */}
          <ExapandableList
            heading="Options"
            classes={classes}
            open={open.options}
            onClick={() => setOpen({ ...open, options: !open.options })}
            style={{ borderBottom: 0 }}
          >
            <h6>Title</h6>
            <Input value={widgetTitle} onChange={(e) => setWidgetTitle(e.target.value)} />
            <h6>Subtitle</h6>
            <Input value={widgetSubtitle} onChange={(e) => setWidgetSubtitle(e.target.value)} />
            <small className="form-text text-muted">Enter custom text (uncheck dynamic titles)</small>
            <h6 className="mt-3">Assets per page</h6>
            <InputGroup className="mb-3">
              <FormControl
                type="number"
                aria-label="assets-per-page"
                value={assetsPerPage}
                onChange={(e) => setAssetsPerPage(e.target.value)}
              />
            </InputGroup>
            <Checkbox label="Show search bar" value="showSearchBar" defaultChecked={showSearchBar} onChange={(e) => setShowSearchBar(e.target.checked)} />
            <Checkbox label="Show more button" value="showMore" defaultChecked={showMore} onChange={(e) => setShowMore(e.target.checked)} />
            <Checkbox label="Use dynamic title" value="dynamicTitle" defaultChecked={dynamicTitle} onChange={(e) => setDynamicTitle(e.target.checked)} />
            <Checkbox label="Use dynamic subtitle" value="dynamicSubtitle" defaultChecked={dynamicSubtitle} onChange={(e) => setDynamicSubtitle(e.target.checked)} />
            {mediaType === 'images' && <Checkbox label="Use Image Search filters" value="searchFilters" defaultChecked={searchFilters} onChange={(e) => setSearchFilters(e.target.checked)} />}
            <Collapse in={searchFilters}>
              <div className={classes.onItemClickBlock}>
                <Editor code={defaultSearchFilter} />
              </div>
            </Collapse>
            <Checkbox label="Use Overlay Actions" value="overlayActions" defaultChecked={overlayActions} onChange={(e) => setOverlayActions(e.target.checked)} />
            <Collapse in={overlayActions}>
              <div className={classes.onItemClickBlock}>
                <Editor code={defaultOverlayActionCode} />
              </div>
            </Collapse>
            <Checkbox label="Use onItemClick callback" value="onItemClick" defaultChecked={onItemClick} onChange={(e) => setOnItemClick(e.target.checked)} />
            <Collapse in={onItemClick}>
              <div className={classes.onItemClickBlock}>
                <Editor code={defaultOnItemClick} />
              </div>
            </Collapse>
          </ExapandableList>
          <Button
            className="btn btn-danger"
            onClick={() => {
            // save all of the changes
            // console.log('yppp');
            setParams({
              name: 'searchPage',
              props: {
                mediaType,
                dynamicSubtitle,
                dynamicTitle,
                onItemClick,
                showMore,
                showSearchBar,
                overlayActions,
                widgetSubtitle,
                widgetTitle,
                assetsPerPage: assetsPerPage > 0 ? assetsPerPage : 26,
                ...(mediaType === 'images' ? {
                  searchFilters,
                }: {})
              }
            });
            setShow(false);
            }}
          >
            Save changes
          </Button>
        </Form>
      </PageConfigModal>
    </>
  )
};

export default SearchPageConfig;
