import { connect } from 'react-redux';

import TabbedCodeSnippet from './TabbedCodeSnippet';
import { getHtmlCode } from './codeSnippets/html-snippet';
import { getReactCode } from './codeSnippets/react-snippet';

const mapStateToProps = (state) => {

  const activePage = state.pages.activePage;
  const page = state.pages[activePage];

  const props = {...state.main, page};

  return {
    codes: [
      {
        language: 'html',
        value: getHtmlCode(props)
      },
      {
        language: 'react',
        value: getReactCode(props),
      }
    ],

  };
};

export default connect(
  mapStateToProps,
)(TabbedCodeSnippet);
