import { useEffect, useRef, useCallback } from 'react';
import insertPreviewSvg from '../../images/insert-preview.svg';

import imageItem from '../../mockData/item.json';
import subscriptions from '../../mockData/subscriptions.json';
import licenseHistory from '../../mockData/licenseHistory.json';

const useSearchWidget = ({
  apiKey,
  htmlElement,
  page = {},
}) => {
  const widgetInstanceRef = useRef();
  const widgetRef = useRef();

  const configureWidget = useCallback(() => {
    let widgetPage = {};
    if (page.name === 'searchPage') {
      widgetPage = {
        name: page.name,
        component: window.ShutterstockWidget.components.SearchPage,
        props: {
          ...page.props,
          mediaType: page.props.mediaType,
          title: page.props.widgetTitle,
          subtitle: page.props.widgetSubtitle,
          ...(page.props.onItemClick && {
            onItemClick: (e, item) => {
              e.preventDefault();
              console.log(item);
            }
          }),
          ...(page.props.searchFilters && {
            searchFilters: {
              showFilterDrawer: true,
              images: {
                imageTypeFilter: true,
                orientationFilter: true
              }
            }
          }),
          ...(page.props.overlayActions && {
            overlayActions: [
              {
                label: 'Insert preview',
                icon:'/static/media/insert-preview.ca5fa11b.svg',
                onClick: (e, item) => {
                  e.preventDefault();
                  console.log(item);
                }
              }
            ],
          }),
          ...(page.props.mediaType === 'videos' ? { searchFilters: false } : {}),
        }
      };
    }

    if (page.name === 'imageDetailsPage') {
      widgetPage = {
        name: page.name,
        component: window.ShutterstockWidget.components.ImageDetailsPage,
        props: {
          item: imageItem,
          ...(page.props.displayButtons && { buttons: [
            {
              label: 'License',
              isPrimary: true,
              onClick: async (e, item) => {
                e.preventDefault();
                console.log(item);
              }
            }
          ]}),
        }
      };
    }

    if (page.name === 'imageLicensingPage') {
      widgetPage = {
        name: page.name,
        component: window.ShutterstockWidget.components.ImageLicensingPage,
        props: {
          item: imageItem,
          subscriptions,
          ...(page.props.displayButtons && { buttons: [
            {
              label: 'License',
              isPrimary: true,
              onClick: async (e, item, props) => {
                e.preventDefault();
                console.log(item);
                console.log(props.subscription);
              }
            }
          ]}),
        },
      };
    }

    if (page.name === 'imageLicenseHistoryPage') {
        widgetPage = {
          name: page.name,
          component: window.ShutterstockWidget.components.ImageLicenseHistoryPage,
          props: {
            ...(page.props.onLicenseHistoryItemClick && {
              onLicenseHistoryItemClick: (item) => {
                // navigate to the image details when a user clicks on image on license history
                console.log(item);
              },
            }),
            licenseHistory
          }
      }
    }

    const widgetConfig = {
      container: widgetRef.current,
      key: apiKey,
      languageCode: 'en',
      pages: [widgetPage],
    };

    // eslint-disable-next-line no-undef
    if (typeof window === 'object' && window.ShutterstockWidget) {
      // eslint-disable-next-line no-undef
      const widgetInstance = new window.ShutterstockWidget(widgetConfig);
      if (page.name === 'searchPage') {
        widgetInstance.getPage(page.name).search();
      } else {
        widgetInstance.getPage(page.name).render();
      }

      widgetInstance.navigateTo(page.name);

      widgetInstanceRef.current = widgetInstance;
    }
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [
    apiKey,
    htmlElement,
    page
  ]);

  useEffect(configureWidget, [
    apiKey,
    htmlElement,
    page
  ]);

  return {
    widgetInstanceRef,
    widgetRef,
  };
};

export default useSearchWidget;
