import { connect } from 'react-redux';

import ImageDetailsPageConfig from './ImageDetailsPageConfig';
import { setPageParams } from '../../actions/pages';

const mapStateToProps = (state) => {
  return { page: state.pages.imageDetailsPage };
};

const mapDispatchToProps = {
  setParams: setPageParams
};

export default connect(
  mapStateToProps,
  mapDispatchToProps,
)(ImageDetailsPageConfig);
