import React, { useState } from 'react';
import { Modal, Button, Form, Row, Col, InputGroup, FormControl, Collapse } from 'react-bootstrap';

import insertPreviewSvg from '../../images/insert-preview.svg';

import RadioButton from '../RadioButton';
import Checkbox from '../Checkbox';
import Input from '../Input';
import ExapandableList from '../ExapandableList';
import CodeSnippetBlock from '../CodeSnippetBlock';
import Editor from '../Editor';
import PageConfigModal from '../PagesConfig/PageConfigModal';

import classes from '../LeftNavMenu/LeftNavMenu.module.scss';

const defaultButtonImageDetailsPage = `[
  {
    label: 'License',
    isPrimary: true,
    onClick: async (e, item) => {
      e.preventDefault();
      console.log(item);
    }
  }
]`;


const ImageDetailsPage = ({
  page,
  setParams,
  activated
}) => {
  const [displayButtons, setDisplayButtons] = useState(page.props.displayButtons);

  const [show, setShow] = useState(false);

  return (
    <>
      <div>
        {activated && (
          <a href="#" onClick={(e) => {
            e.preventDefault();
            setShow(true)
          }}> Edit</a>
        )}
      </div>
      <PageConfigModal show={show} setShow={setShow} pageDisplayName="Image Details Page">
        <Form>
          <Checkbox label="Display custom buttons" value="displayButtons" defaultChecked={displayButtons} onChange={(e) => setDisplayButtons(e.target.checked)} />
          <Collapse in={displayButtons}>
            <div className={classes.onItemClickBlock}>
              <Editor code={defaultButtonImageDetailsPage} />
            </div>
          </Collapse>
          <Button
            className="btn btn-danger mt-3"
            onClick={() => {
            // save all of the changes
              setParams({
                name: 'imageDetailsPage',
                props: {
                  displayButtons,
                }
              });
              setShow(false);
            }}
          >
            Save changes
          </Button>
        </Form>
      </PageConfigModal>
    </>
  )
};

export default ImageDetailsPage;
