import { connect } from 'react-redux';

import ImageLicenseHistoryPage from './ImageLicenseHistoryPage';
import { setPageParams } from '../../actions/pages';

const mapStateToProps = (state) => {
  return { page: state.pages.imageLicenseHistoryPage};
};

const mapDispatchToProps = {
  setParams: setPageParams
};

export default connect(
  mapStateToProps,
  mapDispatchToProps,
)(ImageLicenseHistoryPage);
