import React, { useState } from 'react';
import { Form, Button, InputGroup, FormControl, Collapse } from 'react-bootstrap';

import RadioButton from '../RadioButton';
import Checkbox from '../Checkbox';
import Input from '../Input';
import ExapandableList from '../ExapandableList';
import CodeSnippetBlock from '../CodeSnippetBlock';
import PagesConfig from '../PagesConfig';
import classes from './LeftNavMenu.module.scss';

const getPageDisplayName = (pageName) => ({
  'searchPage': 'Search Page',
  'imageDetailsPage': 'Image details Page',
  'imageLicensingPage': 'Image Licensing Page',
  'imageLicenseHistoryPage': 'Image License History Page'
})[pageName];

const LeftNavMenu = ({
  inputs,
  setParams,
  page
}) => {
  const [apiKey, setApiKey] = useState(inputs.apiKey);
  const [htmlElement, setHtmlElement] = useState(inputs.htmlElement);

  const [open, setOpen] = useState({
    options: true,
    htmlContainer: true,
    apiKey: true,
    configurePages: true
  });

  return (
    <div className={classes.container} style={{ marginTop: 20 }}>
      <ExapandableList
        heading="HTML container"
        classes={classes}
        open={open.htmlContainer}
        onClick={() => setOpen({ ...open, htmlContainer: !open.htmlContainer })}
      >
        <Input value={htmlElement} onChange={(e) => setHtmlElement(e.target.value)} />
        <small className="form-text text-muted">Element you want the widget to render in.</small>
      </ExapandableList>

      <ExapandableList
        heading="API key"
        classes={classes}
        open={open.apiKey}
        onClick={() => setOpen({ ...open, apiKey: !open.apiKey })}
      >
        <Input value={apiKey} onChange={(e) => setApiKey(e.target.value)} />
        <small className="form-text text-muted">Enter your API application's consumer key</small>
      </ExapandableList>
      <ExapandableList
        heading="Configure pages"
        classes={classes}
        open={open.configurePages}
        onClick={() => setOpen({ ...open, configurePages: !open.configurePages })}
      >
        <PagesConfig />
      </ExapandableList>
      <div className="mt-4 mb-5 text-center">
        <Button className="btn btn-danger" style={{ borderRadius: '25px' }} onClick={() => {
          setParams({
            inputs: {
              apiKey,
              htmlElement,
            },
            page
          });
        }}>Load widget</Button>
      </div>
    </div>
  );
};

export default LeftNavMenu;
