import { connect } from 'react-redux';

import LeftNavMenu from './LeftNavMenu';
import { setParams } from '../../actions/main';

const mapStateToProps = (state) => {

  const activePage = state.pages.activePage;
  return {
    ...state.main,
    page: state.pages[activePage]
  };
};

const mapDispatchToProps = {
  setParams
};

export default connect(
  mapStateToProps,
  mapDispatchToProps,
)(LeftNavMenu);
