import { connect } from 'react-redux';

import ImageLicensingPageConfig from './ImageLicensingPageConfig';
import { setPageParams } from '../../actions/pages';

const mapStateToProps = (state) => {
  return { page: state.pages.imageLicensingPage };
};

const mapDispatchToProps = {
  setParams: setPageParams
};

export default connect(
  mapStateToProps,
  mapDispatchToProps,
)(ImageLicensingPageConfig);
