import React, { useState } from 'react';
import { Modal, Button, Form, Row, Col, InputGroup, FormControl, Collapse } from 'react-bootstrap';

import insertPreviewSvg from '../../images/insert-preview.svg';

import RadioButton from '../RadioButton';
import Checkbox from '../Checkbox';
import Input from '../Input';
import ExapandableList from '../ExapandableList';
import CodeSnippetBlock from '../CodeSnippetBlock';
import Editor from '../Editor';
import PageConfigModal from '../PagesConfig/PageConfigModal';

import classes from '../LeftNavMenu/LeftNavMenu.module.scss';

const licenseHistoryCallBack = `onLicenseHistoryItemClick: (item) => {
  console.log(item);
},`;

const ImageLicenseHistoryPage = ({
  page,
  setParams,
  activated
}) => {
  const [onLicenseHistoryItemClick, setOnLicenseHistoryItemClick] = useState(page.props.onLicenseHistoryItemClick);

  const [show, setShow] = useState(false);

  return (
    <>
      <div>
        {activated && (
          <a href="#" onClick={(e) => {
            e.preventDefault();
            setShow(true)
          }}> Edit</a>
        )}
      </div>
      <PageConfigModal show={show} setShow={setShow} pageDisplayName="Image Details Page">
        <Form>
          <Checkbox label="License History Item click callback" value="onLicenseHistoryItemClick" defaultChecked={onLicenseHistoryItemClick} onChange={(e) => setOnLicenseHistoryItemClick(e.target.checked)} />
          <Collapse in={onLicenseHistoryItemClick}>
            <div className={classes.onItemClickBlock}>
              <Editor code={licenseHistoryCallBack} />
            </div>
          </Collapse>
          <Button
            className="btn btn-danger mt-3"
            onClick={() => {
            // save all of the changes
              setParams({
                name: 'imageLicenseHistoryPage',
                props: {
                  onLicenseHistoryItemClick,
                }
              });
              setShow(false);
            }}
          >
            Save changes
          </Button>
        </Form>
      </PageConfigModal>
    </>
  )
};

export default ImageLicenseHistoryPage;
