import React from 'react';
import useSearchWidget from './useSearchWidget';
import classes from './SearchWidget.module.scss';

const SearchWidget = ({
  inputs: {
    apiKey,
    htmlElement,
  },
  page,
}) => {
  const { widgetRef } = useSearchWidget({
    apiKey,
    htmlElement,
    page,
  });

  return (
    <div className={classes.container}>
      <div ref={widgetRef} />
    </div>
  );
};

export default SearchWidget;
